<template>
    <div class="container-results">

        <h2 class="u-mtl u-mbm u-sm-mts u-sm-mbs"> Results </h2>

        <section class="res-header">
            <div>
                <h3>With Tags</h3>
                <p>We cached this test for 60min</p>
            </div>
            <div>
                <h3>No tags </h3>
                <p class="tags" v-if="blockedTags">{{blockedTags}}</p>
            </div>
        </section>
        
        <iframe v-if="result.originalTestResult" :src="`${VUE_APP_API_LIGHTHOUSE}/report/?report=${result.originalTestResult.report}`"></iframe>
        <iframe v-if="result.blockedTestResult" :src=" `${VUE_APP_API_LIGHTHOUSE}/report/?report=${result.blockedTestResult.report}`"></iframe>

    </div>
    <!-- container -->
</template>

<script>
    import {
        serverBus
    } from '../main.js';

    export default {
        name: 'lh-results',
        props: ['content'],
        data: () => ({
            result: {},
            blockedTags: false,
            VUE_APP_API_LIGHTHOUSE:null
        }),
        methods: {
            async getJSONdata(path) {

                const data = await fetch(`${process.env.VUE_APP_LIGHTHOUSE_ENDPOINT}/index.php?path=${path}`)
                if (!data.ok) {
                    const errorText = await data.text();
                    throw new Error(errorText);
                }
                const json = await data.json();
                return json;
            },
            async compare() {

                const getOriginalJSON = this.getJSONdata(this.result.originalTestResult.reportJSON);
                const getBlockedJSON = this.getJSONdata(this.result.blockedTestResult.reportJSON);

                const [originalJSON, blockedJSON] = await Promise.all([getOriginalJSON, getBlockedJSON]);

                console.log(originalJSON.categories.performance.score)
                console.log(blockedJSON.categories.performance.score)
            }

        },

        created() {

            serverBus.$on('reportsReady', (result) => {
                this.result = result;
                
                this.VUE_APP_API_LIGHTHOUSE = process.env.VUE_APP_API_LIGHTHOUSE;
                //this.compare().catch(e => console.log(e));
            });

             serverBus.$on('blockedTags', (blockedTags) => {

                   this.blockedTags = blockedTags.map(e => e.url).join(' ');
             });
        }
    }
</script>

<style>
    .container-results {
        margin-left: 10%;
        margin-right: 10%;
        min-height: 4000px;
    }

    iframe {
        display: inline-block;
        width: 50%;
        height: 4000px !important;
        padding: 3px;
    }

    @media only screen and (max-width: 1440px) {
        .container-results {
            margin-left: 5%;
            margin-right: 5%;
        }
    }

    section.res-header div {
        display: inline-block;
        text-align: center;
        width: 50%;
        vertical-align: top;
    }

    section.res-header div p.tags {
        font-family: monospace;
        background-color: #75757554;
        column-count: 3;
        text-align: left;
        padding: 8px;
        column-rule: solid 1px #ffffff52;
    }
</style>