<template>
  <div id="app" class="clearfix bg-blue banner-full-screen u-pbl">


    <header>

      <lhHeader :content="content" @stepUpdate="moveToSection($event)"></lhHeader>

    </header>


    <lhTable :content="content"  @stepUpdate="moveToSection($event)" id="table" class="why u-mtl u-mbl u-pbl u-txtCenter" v-show="visibleSection.includes('table')"></lhTable>

    <lhResults :content="content" @stepUpdate="moveToSection($event)" id="results" class="why u-mtl u-mbl u-pbl u-txtCenter" v-show="visibleSection.includes('results')"></lhResults>

  </div>
</template>

<script>
  import css from '../static/css/app.css'
  import lhHeader from './components/header'
  import lhTable from './components/table'
  import lhResults from './components/results'

  export default {
    name: 'app',
    components: {
      lhHeader,
      lhTable,
      lhResults
    },
    data() {
      return {
        content: '',
        visibleSection: [],
      }
    },
    methods: {
      moveToSection: function (event) {

        this.visibleSection.push(event)
        this.scrollTo('#' + event);
      },

      scrollTo: function (e) {
        setTimeout(() => { this.$scrollTo(e, 500) }, 100)
      },



    }


  }
</script>

<style>
@import 'https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900|Material+Icons';

.banner-full-screen{
  background-image:url('./assets/wallpaper.png')
}
</style>