<template>
    <div class="container">

        <img src="../assets/price-tag.svg" width="200px" class="img-center img-responsive u-mtm u-sm-mtm" alt="">


        <h1 class="u-txtCenter u-mts"> Lighthouse tag tester</h1>

        <p class="u-txtCenter u-txtcatchline">Check how much the tags affect performance </p>

        <div class="form-group">
            <input v-model="url" id="url" class="form-input block-center" placeholder="Fill in a web-address (URL)"
                type="url">
        </div>

        <button href="#" @click.prevent="sendUrl" id="submitFeedback" class="block-center button button--big u-plm u-prm u-sm-pln u-sm-prn"
            :disabled="isLoading">Detect
            third party tags</button>

        <p v-show="urlValid === false" class="ab-error u-txtCenter">Please enter a valid url</p>

        <p v-show="error.message !== ''" class="ab-error u-txtCenter">{{error.message}}</p>


        <section v-show="isLoading" class="loading">

            <v-progress-circular class="u-mtm block-center" :size="50" color="amber" indeterminate></v-progress-circular>

            <p class="u-txtCenter">We are detecting the tags on {{url}}... this can take a bit of
                time
                especially if the tested page is slow</p>
        </section>



    </div> <!-- /container -->
</template>

<script>
    import {
        serverBus
    } from '../main.js';

    import { captureException} from '@sentry/browser';

    export default {
        name: 'lh-header',
        props: ['content'],
        data() {
            return {
                url: '',
                urlValid: null,
                isLoading: false,
                error: {
                    message: ''
                }
            }
        },
        methods: {

            sendUrl: function () {
                console.log(process.env)
            
                this.error.message = "";

                this.urlValid = (this.$validator.isURL(this.url)) ? true : false;

                if (this.urlValid) {

                    userInfo.url = this.url;

                    this.runOriginalTest()
                        .then( r => console.log(r) )
                        .catch( e =>{
                            console.log(e);
                            captureException(e);
                        });

                    this.getThirdParty().then(json => {

                            this.isLoading = false
                            serverBus.$emit('tagsDetected', json);
                            this.$emit('stepUpdate', 'table');

                        })
                        .catch(e => {

                            captureException(e);

                            this.isLoading = false;

                            e = e.toString();

                            if (e.includes('ERR_NAME_NOT_RESOLVED')) {

                                return this.error.message = "Can't access this url, this domain doesn't exist";
                            }

                            this.error.message = e;
                        });

                    //this.visibleSection.push('why');
                }

            },

            async runOriginalTest() {

                //console.log('Sent a request to get the original test ')

                // const data = await fetch(`${process.env.VUE_APP_API_LIGHTHOUSE}/runOriginalTest`, {
                //     method: 'post',
                //     headers: {
                //         'Content-Type': 'application/json'
                //     },
                //     body: JSON.stringify({
                //         url: userInfo.url
                //     })
                // });

                // if (!data.ok) {
                //     const errorText = await data.text();
                //     throw new Error(errorText);
                // }
                // const {body,ok,error} = await data.json();


                // if(!ok){
                //     throw new Error(error);
                // }

                // return body;

            },

            async getThirdParty() {

                this.isLoading = true;
                // &mocked=true 
                const data = await fetch(`${process.env.VUE_APP_API_PUPPETEER}/get3p?url=${this.url}`);

                if (!data.ok) throw new Error(await data.text());

                const {body,ok,error} = await data.json();

                if(!ok){
                    throw new Error(error);
                }

                if (body.shouldntBeBlocked.includes('datadome.co')){
                    throw new Error("The tool has been blocked by the website... nothing we can do about it... sorry");
                } 

                return body;
            }
        },
    }
</script>
<style>
    .form-input {
        background-color: #FFF;
        color: black;
    }

    .error {
        background-color: initial !important;
        border-color: initial !important;
    }
</style>