<template>

    <div class="container">

        <h2 class="u-mtl u-mbm u-sm-mts u-sm-mbs"> which tags do you want to block ? </h2>

        <p> We already selected marketing tags, but we maybe missed some, feel free to customize the blocking list </p>

        <div class="row u-mtm">

            <!-- <md-table class="tags-table" v-model="tags" md-card @md-selected="onSelect">

                <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="multiple" md-auto-select>

                    <md-table-cell md-label="url" md-sort-by="email">{{ item.url }}</md-table-cell>
                    <md-table-cell md-label="shouldBeBlocked" md-sort-by="shouldBeBlocked">{{ item.shouldBeBlocked }}</md-table-cell>

                </md-table-row> -->



            <v-app id="inspire" class="tags-table">
                <v-data-table v-model="tags" :headers="headers" :items="tags" item-key="name" :disable-initial-sort="true"
                    hide-actions class="elevation-1">
                    <template slot="items" slot-scope="props">

                        <td class="checkbox-small">
                            <v-checkbox :input-value="isSelected(props.item)" @change="toggle(props.item)" primary
                                hide-details></v-checkbox>
                        </td>
                        <td>{{ props.item.url }}</td>
                        <!-- <td>{{ props.item.shouldBeBlocked }}</td> -->

                    </template>
                </v-data-table>
            </v-app>

            <button @click.prevent="selectTags" class="block-center button button--big u-plm u-prm u-sm-pln u-sm-prn u-mtm"
                :disabled="isLoading">{{getAlternateLabel()}}</button>

            <section v-show="isLoading" class="loading">

                <v-progress-circular class="u-mtm block-center" :size="50" color="amber" indeterminate></v-progress-circular>

                <p v-show="isLoading" class="u-txtCenter"> <strong>Please wait ! We are running 2 LightHouse reports
                        for
                        the site. <br /> It can take a bit of time especially if the site is slow</strong></p>

                <p v-show="error.message !== ''" class="ab-error u-txtCenter">{{error.message}}</p>
            </section>
            <!-- /for -->

        </div>

    </div>
    <!-- container -->

</template>

<script>
    import {
        serverBus
    } from '../main.js';

    import {
        captureException
    } from '@sentry/browser';

    export default {
        name: 'lh-table',
        props: ['content'],
        data: () => ({
            selected: [],
            tags: [],
            isLoading: false,
            error: {
                message: ''
            },
            headers: [{
                    text: 'Tag to be blocked',
                    value: 'shouldBeBlocked'
                },
                {
                    text: 'Url',
                    value: 'url',
                    align: 'center'
                }
            ],
        }),
        methods: {
            onSelect(items) {
                this.selected = items;
            },
            getAlternateLabel(count) {
                let plural = ''

                if (this.selected.length > 1) {
                    plural = 's'
                }

                return `Block ${this.selected.length} tag${plural}`;
            },
            isSelected(item) {

                if (this.selected.includes(item)) return true;

                return false;

            },
            toggle(element) {


                if (this.selected.includes(element)) {

                    //IS ALREADY SELECTED WE REMOVE IT
                    this.selected = this.selected.filter(e => e !== element);

                    console.log("THE NEW VALUE", this.selected);
                } else {
                    // IS NOT SELECTED WE ADD IT
                    this.selected.push(element)
                }

            },
            toggleAll() {

                console.log('TOGLE ALL')
                if (this.selected.length) this.selected = []
                else this.selected = this.tags.slice()
            },
            changeSort(column) {
                if (this.pagination.sortBy === column) {
                    this.pagination.descending = !this.pagination.descending
                } else {
                    this.pagination.sortBy = column
                    this.pagination.descending = false
                }
            },
            selectTags() {
                this.isLoading = true;
                this.runTest()
                    .then(r => {

                        this.isLoading = false;

                        if (!r.blockedTestResult.ok) {
                            this.error.message =
                                "Something wrong happened with the blocked test... See message in the JavaScript console";
                            console.log(r.blockedTestResult.message);
                            return;
                        }

                        if (!r.originalTestResult.ok) {
                            this.error.message =
                                "Something wrong happened with original LightHouse test... See message in the JavaScript console";
                            console.log(r.originalTestResult.message);
                            return;
                        }


                        this.$emit('stepUpdate', 'results');

                        setTimeout(() => {

                            serverBus.$emit('reportsReady', r);

                            serverBus.$emit('blockedTags', this.selected);

                        }, 800);

                    })
                    .catch(e => {
                        this.isLoading = false;
                        captureException(e);
                        this.error.message =
                            "Something wrong happened with original LightHouse test... See message in the JavaScript console";
                        console.log(e)
                    })


            },
            async runTest() {

            
                const data = await fetch(`${process.env.VUE_APP_API_LIGHTHOUSE}/runtest`, {
                    method: 'post',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        //mocked: true,
                        url: userInfo.url,
                        blockedRequests: this.selected.map(e => e.url)
                    })
                });

                if (!data.ok) {
                    const errorText = await data.text();
                    throw new Error(errorText);
                }

                const {body,ok,error} = await data.json();

                if(!ok){
                    throw new Error(error);
                }

                return body;

            }
        },

        created() {

            serverBus.$on('tagsDetected', (tags) => {

                const shouldntBeBlocked = tags.shouldntBeBlocked.map(domain => {
                    return {
                        url: domain,
                        shouldBeBlocked: false
                    }
                });


                const shouldBeBlocked = tags.shouldBeBlocked.map(domain => {
                    return {
                        url: domain,
                        shouldBeBlocked: true
                    }
                });


                this.tags = shouldBeBlocked.concat(shouldntBeBlocked);

                this.selected = shouldBeBlocked;

            });
        }
    }
</script>

<style>
    .tags-table {
        width: 60%;
        display: block;
        margin: auto;
    }

    .checkbox-small {
        width: 50px;
    }

    .v-input--checkbox {
        display: block !important;
        margin: auto !important;
        width: 30px;
    }

    .application--wrap {
        min-height: initial !important;
    }
</style>